.formControlInner{position:relative}.inputWrapper{position:relative;width:100%;min-height:56px;background:#f9f9fa;border-radius:4px}.input{position:relative;background:none;border:none;outline:none;display:block;height:100%;width:100%;margin:0;text-overflow:ellipsis;padding:26px 40px 12px 12px;font-size:14px;line-height:18px;letter-spacing:.1px;color:#363636;border-bottom:1px solid #bfc4c9}.input.error{border-color:#f42c4f}.errorHint{color:#f42c4f}.hint{margin:2px 0 0 12px}input.password{-webkit-text-security:disc;text-security:disc;letter-spacing:.5px}.placeholder{position:absolute;left:12px;right:0;top:50%;transform:translateY(-50%);transition:all .25s linear;font-weight:400;font-size:12px;line-height:16px;letter-spacing:.1px;color:#363636}.placeholder.filled{font-weight:500;line-height:16px;color:#a7a7a7;letter-spacing:.2px;top:6px;transform:translateY(0)}.iconsWrapper{position:absolute;top:50%;right:16px;transform:translateY(-50%);display:flex;align-items:center;gap:0 4px}.eyeButton{padding:0;background:none;border:none;outline:none;width:24px;height:24px;cursor:pointer}input:-webkit-autofill,input:-webkit-autofill:hover,input:-webkit-autofill:focus,input:-webkit-autofill{--webkit-text-fill-color: #ссс;--webkit-box-shadow: 0 0 0px 1000px transparent inset;transition:background-color 3000s ease-in-out 0s}