@use "../../../scss/variables" as *;

.wrapper {
  width: 100%;
  display: flex;
  flex-direction: row;
  column-gap: 30px;
  align-items: center;
  justify-content: flex-start;

  .avatarMask {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 160px;
    height: 160px;
    border-radius: 50%;
    position: relative;
    overflow: hidden;

    box-shadow: $box-shadow;

    .fileUpload {
      display: none;
    }

    .mask {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      //background: #363636;
      background: linear-gradient(0deg, #363636 26%, transparent 26%);
      opacity: 0.7;
      border-radius: 50%;
      border: unset;
      color: transparent;

      &:hover {
        opacity: 0.9;
      }

      &::after {
        cursor: pointer;
        content: '';
        position: absolute;
        display: block;
        width: 32px;
        height: 32px;
        bottom: 5px;
        left: 50%;
        transform: translateX(-50%);
        background-image: url(../../../assets/images/photo-icon.svg);
        background-position: center;
        background-repeat: no-repeat;
      }
    }

    .submit {
      position: absolute;
      top: 0;
    }
  }

  .profileName {
    display: flex;
    flex-direction: column;
    row-gap: 8px;
    font-weight: 500;
    font-size: 52px;
    line-height: 1.23;
    letter-spacing: 0.2px;
  }

  .avatar {
    width: 100%;
    height: 100%;
  }

  @media (max-width: 568px) {
    flex-direction: column;
    row-gap: 24px;
    .profileName {
      font-size: 26px;
      line-height: 138%;
      text-align: center;
    }
  }
}

